import { useState } from 'react';
import { Btn, Form, InputName, Label } from './ContactForm.styled';
import { useDispatch, useSelector } from 'react-redux';
import { arrContacts } from 'Redux/selectors';
import { addContact } from 'Redux/operations';

export const ContactForm = () => {
  const [name, setName] = useState('');
  const [number, setNumber] = useState('');
  const items = useSelector(arrContacts);

  const dispatch = useDispatch();

  const handleChange = e => {
    const { name, value } = e.target;
    switch (name) {
      case 'name':
        setName(value);
        break;
      case 'number':
        setNumber(value);
        break;
      default:
        return;
    }
  };

  const onSubmit = e => {
    e.preventDefault();
    if (items.find(contact => contact.name === name)) {
      window.alert(`Contact ${name} is already in contacts`);
      return;
    }
    dispatch(addContact({ name, number }));
    reset();
  };

  const reset = () => {
    setName('');
    setNumber('');
  };

  return (
    <Form onSubmit={onSubmit}>
      <Label htmlFor="InputName">Name</Label>
      <InputName
        onChange={handleChange}
        value={name}
        type="text"
        name="name"
        id="InputName"
        pattern="^[a-zA-Z---]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
        title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
        required
      />
      <Label htmlFor="InputNumber">Number</Label>
      <InputName
        onChange={handleChange}
        value={number}
        id="InputNumber"
        type="tel"
        name="number"
        pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
        title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
        required
      />
      <Btn type="submit">add contact</Btn>
    </Form>
  );
};
